import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { UserService } from '../_services/user.service';
import { setCookie } from '../_services/Cookies';
import { AuthService,SocialUser } from "angularx-social-login";
import { GoogleLoginProvider} from "angularx-social-login";
import { Router } from '@angular/router';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  formLogin: FormGroup;
  constructor(private userservice: UserService,private authService: AuthService,private route: Router,private spiner: Ng4LoadingSpinnerService) {
    this.formLogin = new FormGroup({
      username: new FormControl(),
      password:new FormControl()
    })
   }

  ngOnInit() {
  }

  login(){
    console.log(this.formLogin.value);
    this.spiner.show();
    this.userservice.login(this.formLogin.value).then(r=>{
      if(!r) window.alert("Tn ng nhp hoc mt khu khng ng!")
      else{
      setCookie("userID",r.userID,2);
      setCookie("tk",r.tk,2);
      setCookie("name",r.name,2);
      window.location.reload();
      this.route.navigate(['Dashboard']);
      }
    }).catch(e=>{
      window.alert("Tn ng nhp hoc mt khu khng ng!")
    })
    this.spiner.hide();
  }

  signInWithGoogle(){
    this.spiner.show();
    this.authService.signIn(GoogleLoginProvider.PROVIDER_ID).then(res=>{
      // console.log(r.idToken);
      this.userservice.loginWithGoole({t:res.idToken}).then(r=>{
        if(r.tk == undefined) {
          this.spiner.hide();
          window.alert("ng nhp bng google khng thnh cng :( !");
          return;
        }
        console.log(r);
        setCookie("userID",r.userID,2);
        setCookie("tk",r.tk,2);
        setCookie("name",r.name,2);
        window.location.reload();
        this.route.navigate(['Dashboard']);
      }).catch(e=>{
        window.alert("My ch khng phn hi!")
      })
    }).catch(err=> window.alert("ng nhp bng google khng thnh cng :( !"));
    this.spiner.hide();
  }

}
